export default {
    data() {
        return {
            mode: false,
            userData: [],
            URL: getApp().globalData.URL,
        };
    },
    onLoad() {
        this.getdata()
    },
    onNavigationBarButtonTap(e) {
        if (e.index === 0) {
            this.to('sign')
        }
    },
    methods: {
        changeMode() {
            this.mode = !this.mode;
        },
        getdata() {
            this.$u.get('/get_user').then(res => {
                if (res.errorCode !== 0) return;
                this.userData = res.data[0]
            })
        },
        to(page) {
            uni.navigateTo({
                url: `../${page}/${page}`
            })

        },
        login() {
            uni.navigateTo({
                url: "../login/login"
            })
        },
        register() {
            uni.navigateTo({
                url: "../register/register"
            })
        },
        outin() {
            uni.clearStorage();
            uni.reLaunch({
                url: "../login/login"
            })
        }
    }
}
